export default {
  name: "Drawer",
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    mask: {
      type: Boolean,
      default: true
    },
    maskClosable: {
      type: Boolean,
      default: true
    },
    mode: {
      type: String,
      default: 'left' // left right
    }
  },
  methods: {
    handleMaskClick () {
      if (!this.maskClosable) {
        return;
      }
      this.$emit('close', {});
    }
  }
}
